<template>
	<view class="box">
		<view
			class="perch"
			:style="{ height: statusBarHeight + navigationBarHeight + 'px' }"
		></view>
		<view
			class="nav-search"
			:style="{
				paddingTop: statusBarHeight + 'px',
				height: navigationBarHeight + 'px',
			}"
		>
			<image class="logo" src="@/static/images/other/logo.png" mode=""></image>
			<navigator url="/pages/search/search" class="input">
				<image
					src="../../static/images/other/search.png"
					mode="aspectFill"
					class="ico"
				></image>
				<view class="tit"><text class="item">搜上职校网</text></view>
			</navigator>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			statusBarHeight: 20,
			navigationBarHeight: 44,
		}
	},
	created() {
		// #ifdef MP-BAIDU
		swan.getSystemInfo({
			success: res => {
				// console.log('getSystemInfo success', res);
				this.statusBarHeight = res.statusBarHeight
				this.navigationBarHeight = res.navigationBarHeight
			},
			fail: err => {
				console.warn('getSystemInfo fail', err)
			},
		})
		// #endif
	},
}
</script>

<style scoped lang="scss">
.perch {
	height: 64px;
}

.nav-search {
	position: fixed;
	z-index: 99999;
	top: 0;
	left: 0;
	width: 100%;
	padding-top: 20px;
	height: 44px;
	background-color: #fff;
	display: flex;
	align-items: center;
	.logo {
		flex-shrink: 0;
		display: block;
		width: 92px;
		height: 32px;
		margin: 0 10px 0 15px;
	}
	.input {
		display: flex;
		height: 32px;
		align-items: center;
		justify-content: center;
		border-radius: 20px;
		padding: 0 10px;
		width: 100%;
		margin-right: 100px;
		background-color: #f8f8f8;
		font-size: 14px;
		color: #999;
	}
	.tit {
		.item {
			@include textEllipsis(1);
		}
	}
	.ico {
		display: block;
		width: 12px;
		height: 12px;
		margin-right: 8px;
	}
}
</style>
